@import "colors";
footer{
    width: 100%;
    height:100vh;
    background-color: $yellow;
    position: fixed;
    bottom:0%;
    z-index:-1;
    padding:13rem 6rem;
    box-sizing: border-box;
    display:grid;
    grid-template-columns:6fr 1fr;
h1{
    color: $pink;
    // font-size:62px;
    // font-family: $text;
    // font-weight: 800;
    // font-style: italic;
    font:italic 800 40px $text;

}
aside{
    display:flex;
    flex-direction:column;
    // align-items: flex-end;


    a{
        color:$pink;
        text-transform: uppercase;
        font:500 14px $text;
        text-decoration:none;
        letter-spacing: 3px;
        line-height: 1.9;
        transition: 0.5s;
    }
}
div{
    color: $pink;
    display: flex;
    flex-direction: column;
    padding:4rem;
    &:last-of-type{
        align-items: flex-end;
    }
    h5{
        font:500 22px $text;
        line-height: 1.2;
        margin:1 rem 0;
    }
    a{
        text-decoration:none;
        font-family:$text;
        color:$pink;

    }
}
p{
    font:500 12px $text;
    color:$pink;
    letter-spacing: 3px;
    line-height:1.4;
    text-transform:uppercase;
    position:absolute;
    bottom:5%;
    left:50%;
    transform:translateX(-50%);




}
}
.footer{
    width:100%;
    height:100vh;
    z-index:-2;
    background-color: transparent;
    position:absolute;

}
